@use "sass:math";
@use "styles/_breakpoints.scss";

.root {
  width: 100%;
}

.navigateBack {
  margin-top: 1rem;
  margin-bottom: 3rem;
}

.wrapper {
  display: flex;
  align-items: center;
  gap: 3.5rem;
  @include breakpoints.down("tablet") {
    flex-direction: column;
  }
}

.flag {
  flex: 1;
  position: relative;
  margin-right: 3rem;
  width: 100%;
  max-width: 550px;

  @include breakpoints.down("laptop") {
    margin-right: 1.5rem;
  }

  @include breakpoints.down("tablet") {
    max-width: 100%;
    margin-right: 0;
  }
  &::after {
    content: "";
    padding-bottom: math.div(100%, math.div(11, 8));
    display: block;
  }
}

.flagImage {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.content {
  flex: 1;
  width: 100%;
}

.title {
  margin-bottom: 2rem;
}

.details {
  display: flex;
  gap: 4rem;
  margin-bottom: 4rem;
  @include breakpoints.down("tablet") {
    flex-direction: column;
  }
}

.group {
  flex: 1;

  li + li {
    padding-top: 0.5rem;
  }
}

.borders {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
}

.borderList {
  display: inline-flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  @include breakpoints.down("tablet") {
    gap: 1rem;
  }
}

.borderListItem {
  font-size: 0.75rem;
  padding: 0.5rem;
  min-width: 6.25rem;
  text-align: center;
  border-radius: 2px;
  background-color: var(--element);
  box-shadow: 0px 0px 6px rgb(0 0 0/ 0.15);
  transition: box-shadow 0.3s;

  &:hover {
    box-shadow: 0px 0px 6px rgb(0 0 0/ 0.3);
  }
}
